import React from 'react';
import styled from 'styled-components';
import {InnerLayout} from '../styles/Layouts';
import Title from '../Components/Title';
import SmallTitle from '../Components/SmallTitle';
import BusinessCenterIcon from '@material-ui/icons/BusinessCenter';
import SchoolIcon from '@material-ui/icons/School';
import ResumeItem from '../Components/ResumeItem';

function Resume() {
    const briefcase = <BusinessCenterIcon />
    const school = <SchoolIcon />
    return (
        <ResumeStyled>
            <Title title={'Resume'}  />
            <InnerLayout>
                <div className="small-title">
                    <SmallTitle icon={briefcase} title={'Working Experience'} />
                </div>
                <div className="resume-content">
                    <ResumeItem 
                        year={'2020 - Present'} 
                        title={'Full Stack Developer'}
                        subTitle={'Remote'}
                        text={'Have Completed 2 Years As A Full Stack Developer And Worked On Numerous Projects Of Different Clients'} 
                    />
                    <ResumeItem 
                        year={'2015 - 2020 '} 
                        title={'Front End Devloper'}
                        text={'Started  As A Front End Developer back in 2015 And had Worked On Numerous Projects Of Different Clients'} 
                    />
                    
                </div>
                <div className="small-title u-small-title-margin">
                    <SmallTitle icon={school} title={'Educational Qualifications'} />
                </div>
                <div className="resume-content ">
                    <ResumeItem 
                        year={'2019 - 2023'} 
                        title={'Computer Science Degree'}
                        subTitle={'PAF-KIET'}
                        text={'Bachelors Degree is In Progress From Paf Kiet In Computer Science will be completed in 2023  '} 
                    />
                    <ResumeItem 
                        year={'2017 - 2019'} 
                        title={'Inter'}
                        subTitle={'Govt Degree Science And Commerce Collage Gulshan-e-Iqbal'}
                        text={' I Got My Inter Education From Govt Degree Science  Collage Gulshan-e-Iqbal In Computer Science'} 
                    />
                    <ResumeItem 
                        year={'2011 - 2017'} 
                        title={'High School Graduation'}
                        subTitle={'Sping Field School'}
                        text={' I Got My Matric Education From Spring Field English Acadmey Gulshan-e-Iqbal In Computer Science'}
                        />
                </div>
            </InnerLayout>
        </ResumeStyled>    
    )
}

const ResumeStyled = styled.section`
    .small-title{
        padding-bottom: 3rem;
    }
    .u-small-title-margin{
        margin-top: 4rem;
    }

    .resume-content{
        border-left: 2px solid var(--border-color);
    }
`;
export default Resume
