import React from 'react'
import styled from 'styled-components';
import FacebookIcon from '@material-ui/icons/Facebook';
import GithubIcon from '@material-ui/icons/GitHub';
import InstgramIcon from '@material-ui/icons/Instagram';
import LinkedIIcon from '@material-ui/icons/LinkedIn';
import Particle from '../Components/Particle';

function HomePage() {
    return (
        <HomePageStyled>
            <div className="particle-con">
                <Particle />
            </div>
            <div className="typography">
                <h1> Muhammad <span>Faisal Malik</span></h1>
                <p className='o'>
                   Full Stack Developer
                </p>
                <div className="icons">
                    <a href="https://www.facebook.com/profile.php?id=100007865326633" className="icon i-facebook">
                        <FacebookIcon />
                    </a>
                    <a href="https://github.com/beingfaisalmalik" className="icon i-github">
                        <GithubIcon />
                    </a>
                    <a href="https://instagram.com/muhammadfaysalmalik/" className="icon i-instagram">
                        <InstgramIcon />
                    </a>
                    <a href="https://www.linkedin.com/in/faisal-malik-2674221a9?fbclid=IwAR3qKDoopxxhRxJAGfnb_cOSlo-WNboFOGDz-2YTjYVoDt91YRxcZduoLsk" className="icon i-LinkedIn">
                        <LinkedIIcon />
                    </a>
                </div>
            </div>
        </HomePageStyled>
    )
}

const HomePageStyled = styled.header`
    width: 100%;
    height: 100vh;
    position: relative;
   .o{
       font-size:1.5rem;
       
   }
    .typography{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
        width: 80%;

        .icons{
            display: flex;
            justify-content: center;
            margin-top: 1rem;
            .icon{
                border: 2px solid var(--border-color);
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 50%;
                transition: all .4s ease-in-out;
                cursor: pointer;
                &:hover{
                    border: 2px solid var(--primary-color);
                    color: var(--primary-color);
                }
                &:not(:last-child){
                    margin-right: 1rem;
                }
                svg{
                    margin: .5rem;
                }
            }

            .i-instagram{
                &:hover{
                   
                    color: red;
                    border-radius: 40% 20%;
                    
                    box-shadow: 10px 10px rgba(0,0,0,.25);
                    border: 3px outset white;
                    
                    
                }
            }
            .i-facebook{
                &:hover{
                    
                    color:  #2153c9;
                    border-radius: 40% 20%;
                    box-shadow: 10px 10px rgba(0,0,0,.25);
                    border: 3px outset white;
                    
                }
            }
            .i-LinkedIn{
                &:hover{
                    
                    color:  ##159dd0;
                    border-radius: 40% 20%;
                    box-shadow: 10px 10px rgba(0,0,0,.25);
                    border: 3px outset white;
                    
                }
            }
            .i-github{
                &:hover{
                    
                    color: #5F4687;
                    border-radius: 40% 20%;
                    box-shadow: 10px 10px rgba(0,0,0,.25);
                    border: 3px outset white;
                    
                }
            
            }
        }
    }
`;

export default HomePage;
